import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { CompanySubscription } from '@biz-nest/commerce-client';
import { FrontendLogger, IsubscriptionPackage } from '@biz-nest/core-client';
import {
  AppNameService, CurrencyService, EventbusService,
  MainBarTitleService, scaleSmooth
} from '@front-shared-lib';
import { Icrumb, runSubscriptionsForSharedUse } from '@main-shared-lib';
import { AdminBackBarComponent } from '../../misc/admin-back-bar/admin-back-bar.component';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { OnePackageComponent } from '../one-package/one-package.component';

@Component({
  selector: 'app-subscription-view',
  standalone: true,
  imports: [
    OnePackageComponent,
    AdminBackBarComponent,
    AdminPageContentComponent
  ],
  animations: [scaleSmooth],
  templateUrl: './subscription-view.component.html',
  styleUrl: './subscription-view.component.scss'
})
export class SubscriptionViewComponent
implements OnInit, OnDestroy {
  cleanup?: () => void;
  companySub?: CompanySubscription;
  subscriptionPackange?: IsubscriptionPackage;
  breadCrumbs: Icrumb[] = [
    {
      name: 'Dashboard',
      icon: 'space_dashboard',
      link: '/'
    },
    {
      name: 'Subscriptions List',
      icon: 'subscriptions',
      link: 'subscription/list'
    },
    {
      name: 'View',
      icon: 'visibility'
    }
  ];

  constructor(
    private eventbus: EventbusService,
    private router: Router,
    public currencyService: CurrencyService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {
    this.mainBarTitle.title = 'Subscriptions Details';
  }

  ngOnInit(): void {
    const handlers = {
      oneCompanySubscription: (data) => {
        FrontendLogger.debug('oneCompanySubscription ', data);
        this.companySub = data;

        if (!this.companySub) {
          this.router.navigate(['..']);

          return;
        }

        this.subscriptionPackange = {
          name: this.companySub.name,
          amount: this.companySub.amount,
          duration: this.companySub.duration,
          active: this.companySub.active,
          features: this.companySub.features
        };
        this.mainBarTitle.title = 'Subscription Details - ' + this.companySub.name;
      }
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;

    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily manage and view your subscription packages for tailored service options.'
    });
  }


  ngOnDestroy() {
    this.cleanup?.();
  }
}
