import { NgClass } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IsubscriptionPackage } from '@biz-nest/core-client';
import { CurrencyService, scaleSmooth, SubscriptionDurationPipe } from '@front-shared-lib';

@Component({
  selector: 'app-one-package',
  standalone: true,
  imports: [
    SubscriptionDurationPipe,
    NgClass
  ],
  animations: [scaleSmooth],
  templateUrl: './one-package.component.html',
  styleUrl: './one-package.component.scss'
})
export class OnePackageComponent {
  @Input({ required: true }) subscriptionPackange!: IsubscriptionPackage;
  @Input() showCustomizeButton = false;
  @Input() showBuyButton = false;
  @Output() makeSubscription = new EventEmitter<IsubscriptionPackage>();
  @Output() customisePlan = new EventEmitter<IsubscriptionPackage>();

  currToggleId?: string;

  constructor(public currencyService: CurrencyService) { }

  toggleScale(id: string) {
    if (id === this.currToggleId) {
      this.currToggleId = '';

      return;
    }
    this.currToggleId = id;
  }
}
